import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { useStoreon } from "storeon/react";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from 'swiper/react';
import { arrow_up, homePng } from "../../images";
import Icon from "../../Views/Icon/Icon";

import 'swiper/css';
import style from './styles/header.module.scss';
import { setCookie, setDataButtonBotton } from "../../helpers";
import cogoToast from 'cogo-toast';

const Header = (

) => {
    const { activeBackMenu, textHeader, tg, selectItemMenu, messagePopup } = useStoreon('activeBackMenu', 'textHeader', 'tg', 'selectItemMenu', 'messagePopup');
    const navigate = useNavigate();
    const { menu, dispatch, schemaColor } = useStoreon('menu', 'schemaColor');

    const [colorScheme, setColorScheme] = useState(window.localStorage.getItem('colorScheme'))
    const [buttons, setButtons] = useState([]);

    useEffect(() => {
        setColorScheme(window.localStorage.getItem('colorScheme'));
    }, [schemaColor]);

    useEffect(() => {

        if (!!messagePopup) {
            const { hide } = cogoToast.warn(
                <div
                    className={style['detali-info__container']}
                    dangerouslySetInnerHTML={{ __html: messagePopup.split(' ').map((el, i) => el.includes('https://') ? `<strong><a style="color: red" href=${el}>${el}</a></strong>` : el).join(' ') }}
                >
                </div>
                , {
                    position: 'top-center',
                    heading: `Уведомление `,
                    style: `marginTop: 15px; width: 90%`,
                    hideAfter: 10,
                    onClick: (e) => {
                        // messagePopup.match(/https:\/\/[^\s\Z]+/i)?.length ? window.open(messagePopup.match(/https:\/\/[^\s]+/i)[0]) : console.log('fack url');
                        dispatch('setMessagePopup', '')
                        return hide()
                    }
                }
            )
        }
    }, [messagePopup])

    useEffect(() => {
        setDataButtonBotton(false, tg);
        tg.onEvent('viewportChanged', (event) => {
            if (!event.isStateStable) {
                tg.expand()
            }
        });
    }, [])

    useEffect(() => {
        dispatch('setMenu')
    }, [])


    useEffect(() => {
        if (!!!menu?.content) return
        setButtons([
            {
                action_page_id: null,
                additional_info: "",
                id: 998,
                image: `${arrow_up}`,
                name: "",
                parent_page_id: 1,
                slug: '',
                text: "goto",
                class: '',
                can_click: {
                    message: "test",
                    redirect_payment: true,
                    status: true,
                },
            },
            {
                action_page_id: null,
                additional_info: "unit",
                id: 999,
                image: `${homePng}`,
                name: "",
                parent_page_id: 1,
                slug: '',
                text: "Главная",
                class: '',
                can_click: {
                    message: "test",
                    redirect_payment: true,
                    status: true,
                },
            },
            ...menu.content.buttons
        ])

    }, [menu?.content?.buttons, schemaColor, textHeader, activeBackMenu])

    const goTo = () => {
        document.querySelector('.goto').scrollIntoView({ block: 'center', behavior: 'smooth' })
    }
    const openURl = (url, text) => {
        const callback = (status = false, link) => {
            if (!!status) {
                return window.open(link);
            }
            return false;
        }
        tg && +tg.version >= 6 ?
            tg.showConfirm(`Перейти в группу?`, e => callback(e, url))
            : console.log('проблемы с телеграмм')
    }



    return (
        <section>
            <div
                className={classNames({
                    [style['header__menu-wrap--top']]: true,
                })}
                style={{
                    minHeight: activeBackMenu ? `51px` : `0px`,
                    height: activeBackMenu ? `51px` : `0px`,
                }}
            >
                <div
                    className={classNames({
                        [style['header__wrap-discription']]: true,
                    })}
                >{textHeader ?? ''}</div>
            </div>
            <div
                className={style['header__menu-wrap--bottom']}
            >

                {
                    menu?.content && ('buttons' in menu?.content) ?

                        <Swiper
                            spaceBetween={13}
                            slidesPerView={buttons?.length}
                            autoplay
                            style={{ height: '100%' }}
                        // onSlideChange={() => console.log('slide change')}
                        // onSwiper={(swiper) => console.log(swiper)}
                        >
                            {
                                buttons?.map((el, i) => {
                                    return (
                                        <SwiperSlide
                                            key={el.id}
                                        >
                                            {
                                                el.id === 998 ?
                                                    <Icon
                                                        width={30}
                                                        height={30}
                                                        image={el.image}
                                                        onClick={goTo}
                                                        className={classNames({
                                                            [style['header__list-items']]: true,
                                                            [style['header__list-items--back']]: colorScheme === 'dark',
                                                            [style['header__select-item']]: +selectItemMenu === el.id,
                                                        })}
                                                    />
                                                    :
                                                    el.can_click?.status ?
                                                        <div
                                                            onClick={() => {

                                                                if (el.id === 30) {
                                                                    dispatch('setItemMenu', null);
                                                                    openURl(el.url, '');
                                                                    return
                                                                }
                                                                if (el.id === 6) {
                                                                    dispatch('setItemMenu', null);
                                                                    window.localStorage.setItem('itemMenu', null)
                                                                    openURl(el.url, '');
                                                                    return
                                                                }

                                                                dispatch('setItemMenu', el.id);
                                                                window.localStorage.setItem('itemMenu', el.id)
                                                                dispatch('context', {})
                                                                el.slug ? dispatch('setActiveHeaderText', true) : dispatch('setActiveHeaderText', false);
                                                                navigate(`/${el.slug}`, {
                                                                    state: {
                                                                        action_page_id: el.action_page_id ?? 1,
                                                                        url: window.location.href.split('/').pop(),
                                                                        type: el.additional_info,
                                                                        name_button: el.text,
                                                                        url_redirect: el.url
                                                                    }
                                                                })
                                                            }}
                                                        >
                                                            <Icon
                                                                width={30}
                                                                height={30}
                                                                image={el.image}
                                                                className={classNames({
                                                                    [style['header__list-items']]: true,
                                                                    [style['header__list-items--back']]: colorScheme === 'dark',
                                                                    [style['header__select-item']]: +selectItemMenu === el.id,
                                                                })}
                                                            />
                                                        </div>
                                                        : <div
                                                            onClick={() => {
                                                                dispatch('setMessagePopup', el.can_click?.message ? el.can_click?.message : '');
                                                                // dispatch('setActiveHeaderText', false)
                                                                dispatch('setItemMenu', 31);
                                                                window.localStorage.setItem('itemMenu', 31)
                                                                if (el.can_click.redirect_payment) {
                                                                    return navigate('/button_my_profile', {
                                                                        state: {
                                                                            name_button: 'мой профиль'
                                                                        }
                                                                    });
                                                                }
                                                                return true;
                                                            }}
                                                        >
                                                            <Icon
                                                                width={30}
                                                                height={30}
                                                                image={el.image}
                                                                className={classNames({
                                                                    [style['header__list-items']]: true,
                                                                    [style['header__list-items--back']]: colorScheme === 'dark',
                                                                    [style['header__select-item']]: +selectItemMenu === el.id,
                                                                })}
                                                            />
                                                        </div>
                                            }
                                        </SwiperSlide>
                                    )
                                })
                            }
                        </Swiper>

                        : null
                }


            </div>
        </section>
    )
}

export default Header;

