import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import Spinner from "../../../../../Views/Spinner";

import style from "./styles/ourWarrantlyAdmin.module.scss";
import { changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger } from "../../../../../helpers";
import Message from "../../../../../Views/Messages/Message/Message";

const OurWarrantlyAdmin = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, tg } = useStoreon("tg");
  const [newText, setNewText] = useState("");
  const [messageInForm, setMessageInForm] = useState({
    textMessage: '',
    colorMessage: ''
  });

  useEffect(() => {

    dispatch("setTextHeader", state?.name_button ?? 'ГАРАНТ ОТ АДМИНИСТРАЦИИ');
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() => {
      return navigate("/button_rating/button_garant_rating", {
        state: {
          name_button: state?.old_name_button ?? "Наши гаранты",
        },
      });
    });

  }, []);

  useEffect(() => {

    const paramsList = {
      url: "/api_get_page/",
      page_id: 7,
      dataRequst: (resAdmin) => {
        handlerWarningInfoMessageResponse(resAdmin, setMessageInForm);
        setNewText(
          resAdmin.result?.message?.text
            .split(" ")
            .map((el, i) =>
              el.includes("https://") ? `<a href=${el}>${el}</a>` : el
            )
            .join(" ")
        );
      },
    };

    dispatch("getContext", paramsList);

  }, []);

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      {!!newText.length ? (
        <React.Fragment>
          <div
            className={style["detali-info__container"]}
            dangerouslySetInnerHTML={{ __html: newText }}
          ></div>
        </React.Fragment>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};
export default OurWarrantlyAdmin;
