import { Formik } from "formik";
import React, { useEffect, useState } from "react";
import { Form } from "react-router-dom";
import { useStoreon } from "storeon/react";
import useDebounce, { debounce, getOptionsFilters, handlerWarningInfoMessageResponse } from "../../helpers";
import { color } from "../../helpers/const";
import Input from "../Input/Input";
import Message from "../Messages/Message/Message";
import TextSearch from "./TextSearch";

const Search = ({
    setIsLoading = () =>{},
    setResaultSearch,
    setTextSearch,
    textSearch
}) => {
    const { dispatch } = useStoreon();  
    const debouncedSearchTerm = useDebounce(textSearch, 500);
    const [messageInForm, setMessageInForm] = useState({
        textMessage: "",
        colorMessage: "",
      });

    const handlerSubmitSearch = data => {
        setMessageInForm(c=>({
            ...c,
            colorMessage: color.bgc__black,
            textMessage: <TextSearch />
        }));

        const params = {
            q: data,
            page: 1,
            page_size: 30,
            setIsLoading: setIsLoading,
            url: '/api_search_filters/',
            dataRequst: res => { 
                console.log({res})
                const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
                if (isWarning) return;

                setMessageInForm({
                    colorMessage: color.bgc__green,
                    textMessage: `найдено ${res?.result?.count ?? 0} совпадений`
                });
                // textMes={`найдено ${res?.result?.count ?? 0} совпадений`}

                if (res?.result?.count) {
                    const copyDataBrands = res?.result?.brands.slice() ?? [];
                    const copyDatamodels = res?.result?.models.slice() ?? [];
                    const copyDataGenerations = res?.result?.generations.slice() ?? [];
                    setResaultSearch(c => ({
                        ...c,
                        ...res.result,
                        brands: [...getOptionsFilters(copyDataBrands)],
                        models:[...getOptionsFilters(copyDatamodels)],
                        generations:[...getOptionsFilters(copyDataGenerations)],

                    }))

                }else{
                    return  setResaultSearch(c => ({ ...c, count: 0, results: [] }));
                }
                
            },
        };
        
        dispatch('getContextSearch', params)
    }

    useEffect(() => {
        if (debouncedSearchTerm) {
            handlerSubmitSearch(debouncedSearchTerm)
        }
    }, [debouncedSearchTerm]);

    return (
        <React.Fragment>
            <Message
                message={messageInForm.textMessage}
                colorMessage={messageInForm.colorMessage}
            />
            <Formik
                initialValues={{
                    q: '',
                    page: 1,
                    page_size: 30
                }}
            >
                {
                    ({ values, handleChange }) => {
                        return (
                            <React.Fragment>
                                <Form
                                    onChange={handleChange}
                                    style={{
                                        width: '100%',
                                        padding: '0 15px',
                                        position: 'relative',
                                    }}
                                >
                                    <Input
                                        value={values.username}
                                        placeholder={'поиск'}
                                        name={'q'}
                                        id={1}
                                        onChange={e => {
                                            const value = e.target.value;
                                            setTextSearch(value)  
                                            if (value.length === 0) {
                                                setMessageInForm({
                                                    colorMessage: color.bgc__green,
                                                    textMessage: ''
                                                });
                                                setResaultSearch(c => ({
                                                    ...c,
                                                    count: 0,
                                                    brands: [],
                                                    models:[],
                                                    generations:[],                            
                                                }));
                                            }                                         
                                        }
                                        }
                                    />
                                </Form>

                            </React.Fragment>
                        )
                    }
                }
            </Formik>
        </React.Fragment>
    )
}

export default Search;