import React, { useEffect, useState } from "react";

const TextSearch = ({textMes = ''}) => {
    const [text, setText ] = useState('Поиск...')
    useEffect(()=>{
        const timer = setTimeout(()=>{
            setText(textMes)
            return () => clearTimeout(timer);
        },1500)
    },[])
    return <b>{text}</b>
}

export default TextSearch;  