import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useStoreon } from 'storeon/react';
import Offset from '../../../../Views/Offset';
import Spinner from '../../../../Views/Spinner';
import Message from '../../../../Views/Messages/Message/Message';
import CardApplicationAuto from '../../../../Views/CardApplicationAuto/CardApplicationAuto';
import Pagination from '../../../../Views/Pagination/Pagination';
import { changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBotton, setDataButtonBottonTrigger, smoothTop } from '../../../../helpers';

const CreateEditParserList = (

) => {
    const { state } = useLocation();
    const navigate = useNavigate();
    const { dispatch, tg } = useStoreon('tg');
    const [messageInForm, setMessageInForm] = useState({
        textMessage: '',
        colorMessage: ''
    });

    const [listAplication, setListAplication] = useState({
        count: 0,
        results: []
    });

    useEffect(() => {
        state?.textMessage
            ? setMessageInForm((c) => ({
                ...c,
                textMessage: state?.textMessage,
                colorMessage: state?.colorMessage ?? "#555050",
            }))
            : console.log("fake");
    }, [state?.textMessage, state?.colorMessage]);

    useEffect(() => {
        window.localStorage.setItem('numApplication', '')
        dispatch('setTextHeader', state?.name_button ?? 'РЕДАКТИРОВАТЬ АВТО');
        dispatch('setActiveHeaderText', true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);
        tg.BackButton.onClick(() => navigate('/button_parsing', {
            state: {
                name_button: state?.old_name_button ?? ''
            }
        }));

    }, []);

    useEffect(() => {

        const paramsList = {
            url: '/api_get_parsings/',
            page: 1,
            page_size: 30,
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
                if (isWarning) return;
                const copyData = res?.result?.results.slice();
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            },
        };
        dispatch('getListApplications', paramsList)

    }, []);

    const changePaginationPage = data => {
        const { page } = data;
        const paramsList = {
            url: '/api_get_parsings/',
            page: page ?? 1,
            page_size: 30,
            dataRequst: res => {
                const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
                if (isWarning) return;
                const copyData = res?.result?.results.slice();
                setListAplication(c => ({
                    ...c,
                    ...res.result,
                    results: [...copyData]
                }))
            },
        };
        dispatch('getListApplications', paramsList)
    }

    return (
        <React.Fragment>
            {
                listAplication.results.length ?
                    <React.Fragment>
                        <Message
                            message={messageInForm.textMessage}
                            colorMessage={messageInForm.colorMessage}
                        />

                        <CardApplicationAuto
                            listAplication={listAplication}
                            pathGoTo={`/button_parsing/button_edit_parsing_form`}
                        />

                        {
                            listAplication.count > 30 ?
                                <Pagination
                                    allCount={listAplication.count}
                                    count={30}
                                    handlerChangePaginations={changePaginationPage}
                                    currentPage={Number(listAplication.current_page)}
                                    location={'center'}
                                />
                                : null
                        }
                        <Offset mb={50} />

                    </React.Fragment>
                    : <Spinner
                        text={'У Вас нет пока заявок'}
                    />
            }

        </React.Fragment>
    )
}
export default CreateEditParserList;