import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import Offset from "../../../../../../Views/Offset";
import CardApplicationReview from "../../../../../../Views/CardApplicationReview/CardApplicationReview";
import Spinner from "../../../../../../Views/Spinner";
import CardApplicationReviewMembers from "../../../../../../Views/CardApplicationReviewMembers";
import Pagination from "../../../../../../Views/Pagination/Pagination";
import { changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from "../../../../../../helpers";
import Message from "../../../../../../Views/Messages/Message/Message";

const OutWarrantlyMembtrsInfo = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, tg } = useStoreon("tg");
  const [dataUser, setDataUser] = useState(JSON.parse(window.localStorage.getItem('dataUserMembers')) ?? {});
  const [isLoadingInfo, setIsLoadingInfo] = useState(false);
  const [data, setData] = useState({});
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    if (state?.dataUser) {
      window.localStorage.setItem('dataUserMembers', JSON.stringify(state?.dataUser))
    }
    if (!window.localStorage.getItem('dataUserMembers')) {

      navigate("/button_rating/button_garant_rating/button_garant_members", {
        state: {
          name_button: state?.old_name_button ?? "Гаранты-участники",
          city_id: state.city_id ?? window.localStorage.getItem('city_id'),
        },
      })
    }

    dispatch(
      "setTextHeader",
      `${state?.dataUser?.user_data?.user_profile_name ?? ""}`
    );
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate("/button_rating/button_garant_rating/button_garant_members", {
        state: {
          name_button: state?.old_name_button ?? "Гаранты-участники",
          city_id: state?.city_id ?? window.localStorage.getItem('city_id'),
        },
      })
    );

  }, []);

  useEffect(() => {
    if (state?.dataUser) {
      setDataUser(state.dataUser);
    }
  }, []);

  useEffect(() => {
    let params = {
      url: "/api_get_city_member_info/",
      user_id: state?.dataUser.id ?? dataUser?.id,
      page_size: 30,
      setIsLoading: setIsLoadingInfo,
      dataRequst: res => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        const copyData = res?.result?.results?.slice();
        setData((c) => ({
          ...res.result,
          results: [...copyData],
        }));
      },
    };
    dispatch("getDataContent", params);
  }, []);

  const changePaginationPage = (data) => {
    const { page } = data;
    const paramsList = {
      url: "/api_get_city_member_info/",
      user_id: dataUser?.id,
      page: page ?? 1,
      page_size: 30,
      setIsLoading: setIsLoadingInfo,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        const copyData = res?.result?.results?.slice();
        setData((c) => ({
          ...res.result,
          results: [...copyData],
        }));
      },
    };
    dispatch("getContext", paramsList);
  };

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      <CardApplicationReviewMembers listAplication={{ results: [dataUser] }} />

      {isLoadingInfo ? (
        <Spinner />
      ) : data?.count ? (
        <React.Fragment>
          <div>найдено {data?.count} отзыв(ов)</div>
          <CardApplicationReview modal listAplication={data} tg={tg} />
        </React.Fragment>
      ) : null}
      {data?.count > 30 ? (
        <Pagination
          allCount={data.count}
          count={30}
          handlerChangePaginations={changePaginationPage}
          currentPage={Number(data.current_page)}
          location={"center"}
        />
      ) : null}
      <Offset mb={50} />
    </React.Fragment>
  );
};
export default OutWarrantlyMembtrsInfo;
