import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { changeStateBackButton, getOptionsFilters, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from "../../../../helpers";
import CardFilters from "../../../../Views/CardFilters";
import Message from "../../../../Views/Messages/Message/Message";
import Search from "../../../../Views/Search";
import ResaultSearch from "../../../../Views/Search/ResaultSearch";

const FiltersUnitGenerations = () => {
  const type_list = "generations";
  const typePage =
    window.location.href.split("/")[window.location.href.split("/").length - 3];
  const [textSearch, setTextSearch] = useState('');
  const brandPage =
    window.location.href.split("/")[window.location.href.split("/").length - 2];
  const modelPage = window.location.href.split("/").pop();

  const [resaultSearch, setResaultSearch] = useState({
    count: 0,
  });
  const [isLoading, setIsLoading] = useState(false);
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, tg } = useStoreon("tg");
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [selectProduct, setSelectProduct] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    dispatch("setTextHeader", state?.name_button ?? "агрегаты");
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate(`/button_filters/${state?.type ?? typePage}/${brandPage}`, {
        state: {
          name_button: state?.old_name_button ?? "агрегаты",
          type: state?.type,
        },
      })
    );

  }, []);

  useEffect(() => {

    const paramsDataPage = {
      url: "/api_get_context/",
      page_id: 32,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        setMessageInForm({
          textMessage: res?.result.buttons.filter(
            (el) => el.send_type === typePage
          )[0].children.children.children.message,
        });
        return;
      },
    };
    dispatch("getGetContextFilters", paramsDataPage);

  }, []);

  useEffect(() => {

    const params = {
      url: "/api_get_filters/",
      type: typePage,
      list: type_list,
      model_id: modelPage,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        const copyData = res?.result?.slice();
        setDataRequestBrand((c) => [...getOptionsFilters(copyData)]);
        const countEnable = copyData
          .map((el) => (el.status ? el.id : null))
          .filter((el) => el !== null);
        setSelectProduct((c) => [...countEnable]);
      },
    };
    dispatch("getContext", params);

  }, []);

  const handlerChangeData = (changeData) => {
    const item = +changeData.value;
    if (selectProduct.includes(item)) {
      setSelectProduct((c) => c.filter((el) => el !== item));
    } else {
      setSelectProduct((c) => [...c, item]);
    }

    let params = {
      url: "/api_set_filters/",
      generation_id: item,
      type: typePage,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        const countEnable = res.result
          .map((el) => (el.status ? el.id : null))
          .filter((el) => el !== null);
        setSelectProduct((c) => [...countEnable]);
        const copyData = res?.result?.slice();
        setDataRequestBrand((c) => [...getOptionsFilters(copyData)]);
      },
      redirectTo: () => { },
    };
    dispatch("setDataCreateRequest", params);
  };

  const handlerChangeDataSearch = (changeData, key) => {
    const item = +changeData.value;
    if (selectProduct.includes(item)) {
      setSelectProduct((c) => c.filter((el) => el !== item));
    } else {
      setSelectProduct((c) => [...c, item]);
    }

    let params = {
      url: "/api_set_filters/",
      [key]: item,
      type: typePage,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
      },
      redirectTo: () => { },
    };
    dispatch("setDataCreateRequest", params);
  };

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />

      <Search
        setIsLoading={setIsLoading}
        setResaultSearch={setResaultSearch}
        setTextSearch={setTextSearch}
        textSearch={textSearch}
      />

      {resaultSearch.count ? (
        <ResaultSearch
          resaultSearch={resaultSearch}
          handlerChangeData={handlerChangeDataSearch}
          selectProduct={selectProduct}
        />
      ) : (
        !(textSearch.length > 1) ?
          <CardFilters
            list={dataRequstBrand}
            handlerChangeData={handlerChangeData}
            localPath={""}
            old_state={state?.dataFilters?.children}
            selectProduct={selectProduct}
            name_button={state?.name_button}
            type_list={type_list}
            type={state?.type}
            model_id
          /> : null
      )}
    </React.Fragment>
  );
};
export default FiltersUnitGenerations;
