import { Form, Formik } from "formik";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import {
  activeButtonBootomForConfirm,
  changeStateBackButton,
  handlerWarningInfoMessageResponse,
  setDataButtonBottonTrigger,
} from "../../../../helpers";
import Button from "../../../../Views/Button/Button";
import Offset from "../../../../Views/Offset";
import TextArea from "../../../../Views/TextArea/TextArea";
import FormUploadImage from "../../../../Views/UploadImage/FormUploadImage";
import Message from "../../../../Views/Messages/Message/Message";

const RequestTransport = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const { dispatch, tg } = useStoreon("tg");
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {

    dispatch("setTextHeader", state?.name_button ?? "ЗАПРОС НА ГРУЗОВЫЕ");
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate("/button_make_request", {
        state: {
          name_button: state?.old_name_button ?? "СОЗДАТЬ ЗАПРОС",
        },
      })
    );

  }, []);

  const handlerSubmitChooseAggrigate = (data, setFieldValue) => {
    const params = {
      ...data,
      url: "/api_create_request/",
      setFieldValue: setFieldValue,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
      },
      redirectTo: res => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        navigate("/button_make_request", {
          state: {
            ...state,
            textMessage: res?.result?.info?.message,
            colorMessage: res?.result?.info?.status ? "#555050" : "#ff0000",
            name_button: state?.old_name_button ?? "СОЗДАТЬ ЗАПРОС",
          },
        })
      },
    };
    dispatch("setDataCreateRequest", params);
    return false;
  };

  const handlerVirtualClick = (values, setFieldValue) => {
    return () => handlerSubmitChooseAggrigate(values, setFieldValue);
  };

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      <Formik
        initialValues={{
          type: state?.type ?? "transport",
          text: "",
          uploadFiles: null,
        }}
        onSubmit={handlerSubmitChooseAggrigate}
      >
        {({ values, setFieldValue }) => {
          return (
            <React.Fragment>
              <Form
                style={{
                  width: "100%",
                  position: "relative",
                }}
              >
                <Offset mt={20} />
                <TextArea
                  className={"textarea-application"}
                  value={values.text}
                  name={"text"}
                  placeholder={"Опишите Вашу заявку"}
                  height={110}
                  onChange={(e) => {
                    const value = e.target.value;
                    setFieldValue("text", value);
                    if (!value) return setDataButtonBottonTrigger(false, tg);
                    activeButtonBootomForConfirm(
                      tg,
                      { ...values, text: value },
                      "text",
                      "Опубликовать"
                    );
                  }}
                />

                <FormUploadImage
                  multiple
                  values={values}
                  setFieldValue={setFieldValue}
                  label={"прикрепить изображения"}
                />

                <Offset mb={50}>
                  <button
                    className="btn-click"
                    onClick={() => handlerVirtualClick(values, setFieldValue)}
                  ></button>

                  {!!!tg.initDataUnsafe?.query_id ? (
                    <Button
                      disabled={!values.text}
                      type={"submit"}
                      addClass={"form-submit__btn"}
                      active={values.text}
                    >
                      <span>
                        <span>создать</span>
                      </span>
                    </Button>
                  ) : null}
                </Offset>
              </Form>
            </React.Fragment>
          );
        }}
      </Formik>
    </React.Fragment>
  );
};
export default RequestTransport;
