import classNames from "classnames";
import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay } from 'swiper';
import Icon from "../Icon/Icon";

import style from './styles/cardApplicationAutoDuplicate.module.scss';
import { Link } from "react-router-dom";
import { useStoreon } from "storeon/react";
import ViewsImage from "../ViewsImage";
import Button from "../Button/Button";

const CardApplicationAutoDuplicate = ({
    listAplication,
    noclick = false,
    pathGoTo = () => { },
    handlerInCatalogApplication = () => { },
    handlerChangeApplication = () => { },
    handlerDeleteApplication = () => { },
    handlerCopiesApplication,


}) => {
    const { dispatch } = useStoreon();
    const openModalImage = (images) => {

        dispatch('openModal', {
            show: true,
            content: <ViewsImage
                images={images}
            />

        })
    }
    return (
        <React.Fragment>
            <div
                className={style['card-application-auto-duplicate__menu-main-container-list']}
            >

                <ul
                    className={style['card-application-auto-duplicate__application-container-list']}
                >
                    {
                        listAplication.results.map(el => {
                            return (
                                <li
                                    key={`list-app-${el.id}`}
                                >
                                    <div
                                        className={style['card-application-auto-duplicate__application-items']}
                                    >
                                        <div
                                        >
                                            <div
                                                className={classNames({
                                                    [style['card-application-auto-duplicate__application-wrap']]: true,
                                                })}
                                            >
                                                <div
                                                    className={style['card-application-auto-duplicate__menu-items-container']}
                                                >

                                                    {
                                                        el.type.type?
                                                            <div
                                                                className={style['card-application-auto-duplicate__menu-items-type']}
                                                            >
                                                                {'Тип: '}{el.type.name}
                                                            </div>
                                                            : null
                                                    }
                                                    {
                                                        el?.date_create?
                                                            <div
                                                                className={style['card-application-auto-duplicate__menu-items-date-create']}
                                                            >
                                                                {el.date_create}
                                                            </div>
                                                            : null
                                                    }
                                                </div>
                                                {/* {
                                                        el.brand
                                                            ?
                                                            <div
                                                                className={style['card-application-auto-duplicate__menu-items-brand']}
                                                            >
                                                                {'Марка: '}{el.brand
                                                                }
                                                            </div>
                                                            : null
                                                    }
                                                    {
                                                        el.model ?
                                                            <div
                                                                className={style['card-application-auto-duplicate__menu-items-model']}
                                                            >
                                                                Модель: {el.model}
                                                            </div>
                                                            : null
                                                    } */}
                                                    {
                                                        el.generation ?
                                                            <div
                                                                className={style['card-application-auto-duplicate__menu-items-year']}
                                                            >
                                                                Авто: {el.generation}
                                                            </div>
                                                            : null
                                                    }

                                                <div
                                                    className={style['card-application-auto-duplicate__menu-items-container']}
                                                    style={{
                                                        width: `100%`
                                                    }}
                                                >


                                                    <div
                                                        className={style['card-application-auto-duplicate__menu-items-text']}
                                                    >
                                                        {el.text}
                                                    </div>
                                                    <Swiper
                                                        modules={[Autoplay]}
                                                        delay={20}
                                                        spaceBetween={50}
                                                        slidesPerView={1}
                                                        className={style['card-application-auto-duplicate__slider-image']}
                                                        autoplay={true}
                                                    >
                                                        {
                                                            el?.images?.length ?
                                                                el.images.map(item => {
                                                                    return <SwiperSlide
                                                                        key={item.id}
                                                                    >
                                                                        <Icon
                                                                            image={item.url}
                                                                            width={50}
                                                                            height={70}
                                                                            onClick={() => el.images.length ? openModalImage(el.images) : null}
                                                                        />
                                                                    </SwiperSlide>
                                                                })
                                                                : null
                                                        }

                                                    </Swiper>
                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                    <Button
                                        onClick={() => handlerCopiesApplication(el.id)}
                                        className={style['card-application-auto-duplicate__application-update-items']}
                                    >дублировать</Button>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </React.Fragment>
    )
}

export default CardApplicationAutoDuplicate;

