import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import { changeStateBackButton, getOptionsFilters, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from "../../../../helpers";
import CardFilters from "../../../../Views/CardFilters";
import Message from "../../../../Views/Messages/Message/Message";

const FiltersRespairSities = () => {
  const { state } = useLocation();
  const idCity = +window.location.href.split("/").pop();
  const navigate = useNavigate();
  const message = state?.dataFilters?.message;
  const { dispatch, tg } = useStoreon("tg");
  const [dataRequstBrand, setDataRequestBrand] = useState([]);
  const [selectProduct, setSelectProduct] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {

    dispatch("setTextHeader", state?.name_button ?? 'НА РЕМОНТ');
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate("/button_filters/respair", {
        state: {
          name_button: state?.old_name_button ?? "НА РЕМОНТ",
          type: "respair",
        },
      })
    );

  }, []);

  useEffect(() => {

    const params = {
      url: "/api_get_context/",
      page_id: state?.action_page_id ?? 2,
      dataRequst: (res) => {

        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;

        setMessageInForm((c) => ({
          ...c,
          textMessage: res?.result?.buttons[3]?.children?.children?.message ?? 'no message',
        }));
        return;
      },
    };
    if(!message){
      dispatch("getGetContextFilters", params);
    }

  }, []);

  useEffect(() => {

    const params = {
      url: "/api_get_filters/",
      type: state?.type ?? 'respair',
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        const copyDataCity = res?.result?.filter((el) => el.id === idCity)[0].citys?.slice();
        setDataRequestBrand((c) => [...getOptionsFilters(copyDataCity)]);
        const countEnable = copyDataCity
          .map((el) => (el.status ? el.id : null))
          .filter((el) => el !== null);
        if (countEnable.length > 0) setSelectProduct((c) => [...countEnable]);
      },
    };
    dispatch("getContext", params);
    if (state?.cities[0]){
      setDataRequestBrand(getOptionsFilters(state?.cities[0]));
    }

  }, []);

  useEffect(() => {
    setMessageInForm((c) => ({
      ...c,
      textMessage: message,
    }));
  }, [message]);

  const handlerChangeData = (changeData) => {
    const item = +changeData.value;
    if (selectProduct.includes(item)) {
      setSelectProduct((c) => c.filter((el) => el !== item));
    } else {
      setSelectProduct((c) => [...c, item]);
    }

    let params = {
      url: "/api_set_filters/",
      type: state?.type ?? "respair",
      city_id: item,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
      },
      redirectTo: () => { },
    };
    dispatch("setDataCreateRequest", params);
  };

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      <CardFilters
        list={dataRequstBrand}
        handlerChangeData={handlerChangeData}
        old_state={state}
        selectProduct={selectProduct}
        model_id={true}
      />
    </React.Fragment>
  );
};
export default FiltersRespairSities;
