import React, { useEffect, useState } from "react";
import { Form, useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import Offset from "../../../../../Views/Offset";
import Spinner from "../../../../../Views/Spinner";
import { changeStateBackButton, getOptions, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger, smoothTop } from "../../../../../helpers";
import Select from "../../../../../Views/Select";
import { Formik } from "formik";
import CardApplicationReviewMembers from "../../../../../Views/CardApplicationReviewMembers";
import Message from "../../../../../Views/Messages/Message/Message";
import SelectDesktop from "../../../../../Views/Select/SelectDesktop";

const OurWarrantlyMembers = () => {
  const { state } = useLocation();
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();
  const { dispatch, tg } = useStoreon("tg");
  const [dataRequstCountry, setDataRequestCountry] = useState([]);
  const [backOptionsCountry, setBackOptionsCountry] = useState([]);
  const [dataRequstMembers, setDataRequestMembers] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {
    
    dispatch("setTextHeader", state?.name_button ?? `Гаранты-участники`);
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() =>
      navigate("/button_rating/button_garant_rating", {
        state: {
          name_button: state?.old_name_button ?? "Наши гаранты",
        },
      })
    );

  }, []);

  useEffect(() => {
    const params = {
      url: "/api_get_garant_members/",
      page: 1,
      page_size: 30,
      dataRequst: (res) => {
        const copyData = res?.result?.slice();
        setDataRequestCountry((c) => [...c, ...getOptions(copyData)]);
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
      },
    };
    dispatch("getContextOurWarrantlyMembers", params);

    if (state?.city_id ?? !!+window.localStorage.getItem('city_id')) {
      let params = {
        url: "/api_get_city_members/",
        city_id: state?.city_id ?? +window.localStorage.getItem('city_id'),
        setIsLoading: setIsLoading,
        dataRequst: (res) => {
          const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
          if (isWarning) return;
          setDataRequestMembers(res.result)
        },
      };
      dispatch("getContext", params);

      const paramsListCountry = {
        url: "/api_get_garant_members/",
        page: 1,
        page_size: 30,
        dataRequst: (res) => {
          const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
          if (isWarning) return;
          const copyData = res?.result?.slice();
          setBackOptionsCountry((c) => [...getOptions(copyData)]);
        },
      };
      dispatch("getContext", paramsListCountry);
    }
  }, []);

  const handlerChangeData = (changeData, setFieldValue) => {
    window.localStorage.setItem('city_id', changeData.value)
    let params = {
      url: "/api_get_city_members/",
      city_id: changeData.value,
      setIsLoading: setIsLoading,
      dataRequst: res => {
        const isWarning = handlerWarningInfoMessageResponse(res, setMessageInForm);
        if (isWarning) return;
        setFieldValue("members", [...res.result])
      },
    };
    dispatch("getContext", params);
  };

  return (
    <React.Fragment>
      <Message
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      {dataRequstCountry.length ? (
        <Formik
          initialValues={{
            optionsCountry: dataRequstCountry,
            members: [],
            country_id: null,
          }}
        >
          {({ values, errors, setFieldValue }) => {
            console.log({ backOptionsCountry });
            return (
              <React.Fragment>
                <Form
                  style={{
                    width: "100%",
                    position: "relative",
                  }}
                >
                  <Offset mt={20} mb={20}>
                    {tg.platform === "tdesktop" ? (
                      <SelectDesktop
                        options={
                          values.optionsCountry.length
                            ? values.optionsCountry
                            : backOptionsCountry
                        }
                        value={values.brand}
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"country"}
                        id={1}
                        style={{ zIndex: 998 }}
                        onClick={(e) => {
                          const value = +e.target.getAttribute("value");
                          handlerChangeData({ value }, setFieldValue);
                          setFieldValue("country_id", value);
                        }}
                      />
                    ) : (
                      <Select
                        options={
                          values.optionsCountry.length
                            ? values.optionsCountry
                            : backOptionsCountry
                        }
                        value={values.brand}
                        placeholder={"сделайте Ваш выбор"}
                        addClass={"select__dropdown-list-car"}
                        name={"country"}
                        id={1}
                        style={{ zIndex: 998 }}
                        onClick={(e) => {
                          const value = +e.target.value;
                          handlerChangeData({ value }, setFieldValue);
                          setFieldValue("country_id", value);
                        }}
                      />
                    )}
                  </Offset>
                </Form>
                {isLoading ? (
                  <Spinner />
                ) : values.members.length || dataRequstMembers.length ? (
                  <React.Fragment>
                    <div
                      style={{
                        color: `var(--text-color)`,
                      }}
                    >
                      {
                        values.members?.length?
                          `Результат запроса: ${values.members?.length}`
                          : null
                      }
                    </div>
                    <CardApplicationReviewMembers
                      listAplication={{
                        results: values.members.length
                          ? values.members
                          : dataRequstMembers,
                      }}
                      cityId={
                        values?.country_id ? values.country_id : state?.city_id ?? +window.localStorage.getItem('city_id')
                      }
                      sudInfo
                    />
                  </React.Fragment>
                ) : null}
              </React.Fragment>
            );
          }}
        </Formik>
      ) : (
        <Spinner />
      )}
    </React.Fragment>
  );
};
export default OurWarrantlyMembers;
